.artist-slider-block{
  img {
    @apply scale-110 transition-all duration-500;
  }
  &:has(a:hover) a:hover,
  &:has(a:focus-visible) a:focus-visible{
    img {
      @apply opacity-100;
    }
  }

  &:has(a:hover) a:not(:hover),
  &:has(a:focus-visible) a:not(:focus-visible){
    img {
      @apply scale-110 grayscale blur-sm opacity-70;
    }
  }
}